import { getQueryRoute } from 'api/queries/query';
export const getRouteForQuery = (query, location, reset = false) => {
    // Reset pagination
    if (reset) {
        if (query.filter && query.filter.offset !== undefined) {
            query.filter = Object.assign(Object.assign({}, query.filter), { offset: 0 });
        }
        if (query.offset !== undefined) {
            query.offset = 0;
        }
    }
    return `${location.pathname}?${getQueryRoute(query)}`;
};
